<style scoped>
    .hierarchy-notification {
        padding: .5em 1em;
        position: relative;
        border-radius: 5px;
        margin: 1em 0;
        min-width: 60%;
        max-width: 100%;
        background: #fff;
        color: #000;
        padding: .8em;
        overflow: auto;
        box-shadow: 0.1em 0.2em 0.5em #e5e5e5;
        display: flex;
        flex-direction: row;
    }
    .hierarchy-notification ul {
        list-style-type: disc;
        margin: .3em 0;
        padding-left: 2em;
    }
    .success {
        border: 1px solid #0c0;
    }
    .important {
        border: 1px solid #f97a6b;
    }
    .info {
        margin-left: 0;
        border: 1px solid #40b8ea;
    }
    .info-message , .success-icon, .attention {
        position: relative;
    }
    .info-message {
        color: #40b8ea;
    }
    .success-message {
        color: #0c0;
    }
    .attention-message {
        color: #f97a6b;
    }
    .hierarchy-notification a {
        color: #326CA6;
    }
    .hierarchy-notification a:hover {
        cursor: pointer;
    }
    .icon-box, .text-box {
        float:left;
    }
    .icon-box {
        padding-top: 0.2em;
        margin-right: .5em;
    }
    .text-box p {
        text-align: left;
        margin: .1em .2em;
        font-family: sans-serif;
        font-size: .8rem;
    }
</style> 
<template>
	 <div class="hierarchy-notification">
        <div v-if="iconName !== 'none'" class="icon-box">
            <icon :name="iconName" :class="iconClass"></icon>
        </div>
        <div class="text-box">
            <slot></slot>
        </div>
    </div>
</template>
<script>
    import { mapGetters, mapActions, mapMutations } from 'vuex';
    import { NAMESPACE as APP_NAMESPACE } from '../store/modules/app.js';
    import 'vue-awesome/icons/check-circle';
    import 'vue-awesome/icons/exclamation-circle';
    import 'vue-awesome/icons/exclamation-triangle';
    import 'vue-awesome/icons/info-circle';
    import Icon from 'vue-awesome/components/Icon.vue';
    
    export default{
        props: ['iconName', 'iconClass'],
        components:{
            'icon': Icon
        }
    }
</script>


