import utils from './../utils';
import namespace from './fieldsMapping/namespace';
import actions from './fieldsMapping/actions';
import state from './fieldsMapping/state';
import getters from './fieldsMapping/getters';
import mutations from './fieldsMapping/mutations';

const STORE = {
	namespaced: true,
	state: state,
	getters: getters,
	actions: actions,
	mutations: mutations
};
const PREFIX = 'fieldsMapping';

const NAMESPACE = utils.publishNamespace(PREFIX, namespace);

export {
	STORE,
	PREFIX,
	NAMESPACE
};


