import utils from './../utils';
import namespace from './hierarchy/namespace';
import actions from './hierarchy/actions';
import state from './hierarchy/state';
import getters from './hierarchy/getters';
import mutations from './hierarchy/mutations';

const STORE = {
	namespaced: true,
	state: state,
	getters: getters,
	actions: actions,
	mutations: mutations
};
const PREFIX = 'hierarchy';
const NAMESPACE = utils.publishNamespace(PREFIX, namespace);

export {
	STORE,
	PREFIX,
	NAMESPACE
};


